/**
 * Create a new HTML element.
 * @param tag HTML tag.
 * @param options HTML attributes.
 */
export function create(tag, options) {
    var element = document.createElement(tag);
    if (options)
        for (var attr in options)
            element.setAttribute(attr, options[attr]);
    return element;
}
/**
 * Transform HTMLCollection to array.
 * @param set HTML collection.
 */
export function toArray(set) {
    var arr = [];
    for (var i = 0; i < set.length; i++)
        arr.push(set[i]);
    return arr;
}
/**
 * Remove HTML element from DOM.
 * @param node HTML element.
 */
export function removeElementFromDOM(node) {
    node.parentNode.removeChild(node);
}
/**
 * Get random key.
 */
export function getRandomKey() {
    return Math.random()
        .toString(36)
        .substr(2, 9);
}
