import { create } from '../utils';
import { INPUT_CLASS } from '../constants';
/**
 * Create a new Input component.
 * @param inputKeyup Callback on keyup event.
 * @param inputBlur Callback on blur event.
 * @param inputPaste Callback on paste event.
 * @param options User options.
 */
export default function createInput(inputKeyup, inputBlur, inputPaste, options) {
    var placeholder = (options && options.inputPlaceholder) || 'add more people...';
    var input = create('input', { class: INPUT_CLASS, type: 'text', placeholder: placeholder });
    input.addEventListener('keyup', inputKeyup);
    input.addEventListener('blur', inputBlur);
    input.addEventListener('paste', inputPaste);
    return input;
}
